<template>
    <div class="twobg" v-else>
        <el-table
          :data="tableData2"
          :show-header="false"
          style="width: 100%;">
          <el-table-column
            type="index"
            width="100px">
          </el-table-column>
          <el-table-column
            prop="title">
          </el-table-column>
          <el-table-column  width='150px'>
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="editVisible3=true">编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  plain
                  @click="delworks">删除</el-button>
              </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="1000">
            </el-pagination>
        </div>
        <!-- 新建入学年份 -->
        <el-dialog title="修改入学年份" class="datas-bg" :visible.sync="editVisible3" width="380px">
            <el-input class="margin10" v-model="input" size="small" placeholder="年份"></el-input>
            <p class="red">* 年份只能是数字</p>
            <p class="red">* 年份不能与现有年份重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible3=false" plain>取 消</el-button>
            </div>
        </el-dialog>
      </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            tableData2:[
              {title:2018},
              {title:2019}
            ],
            editVisible3:false
        };
    },
    methods: {
        delworks(){
          this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
            })
            .catch(() => {});
        }
    }
};
</script>
<style lang='scss'>
  .cont{
      tbody{
        .el-table__expanded-cell{
          padding:5px 0 5px 60px;
        }
      }
    }
</style>
<style lang='scss' scoped>
    .red{
      color:red;
      font-size:12px;
      line-height:16px;
      padding-top:5px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
    .cont{
      padding-top:20px;
      .class-bg{
        overflow:hidden;
        line-height:32px;
        font-size:13px;
        color:#666;
      }
      .college{
        margin-right:-10px;
      }
    }
    .pagination{
        margin-top:20px;
    }
</style>


