<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-s-operation"></i> 学校组织管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-radio-group size="small" v-model="choosed">
                      <el-radio-button label="院系管理"></el-radio-button>
                      <el-radio-button label="入学年份管理"></el-radio-button>
                    </el-radio-group>
                    <el-button v-if="choosed=='院系管理'" class="fr" type="primary" @click="editVisible=true" size="small">新建院系</el-button>
                    <el-button v-else class="fr" type="primary" @click="editVisible3=true" size="small">新建入学年份</el-button>
                </div>
                <div class="cont">
                    <div class="onebg"  v-if="choosed=='院系管理'">
                      <el-table
                        :data="tableData5"
                        :show-header="false"
                        style="width: 100%">
                        <el-table-column type="expand" >
                          <template slot-scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                              <div class="class-bg" v-for="item in props.row.datas">
                                <p class="fl">班级 54154616161</p>
                                <div class="fr">
                                  <el-button
                                    size="mini"
                                    @click="editVisible2=true">编辑</el-button>
                                  <el-button
                                    size="mini"
                                    plain
                                    type="danger"
                                    @click="delworks">删除</el-button>
                                </div>
                              </div>
                            </el-form>
                          </template>
                        </el-table-column>
                        <el-table-column
                          prop="title">
                        </el-table-column>
                        <el-table-column>
                          <div class="fr college">
                            <el-button
                            size="mini"
                            type="primary"
                            plain
                            @click="editVisible2=true">新建班级</el-button>
                            <el-button
                            size="mini"
                            @click="editVisible=true">编辑</el-button>
                          <el-button
                            size="mini"
                            type="danger"
                            plain
                            @click="delworks">删除</el-button>
                          </div>
                        </el-table-column>
                      </el-table>

                      <div class="pagination">
                          <el-pagination
                            background
                            layout="prev, pager, next"
                            :total="1000">
                          </el-pagination>
                      </div>
                    </div>

                    <Aschoolmanage2 v-else/>

                </div>
            </div>
        </div>
        <!-- 新建院系 -->
        <el-dialog title="新建院系" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="input" size="small" placeholder="院系名称"></el-input>
            <p class="red">* 新建院系名称不能与现有院系重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible=false" plain>取 消</el-button>
            </div>
        </el-dialog>
        <!-- 新建班级 -->
        <el-dialog title="新建班级" class="datas-bg" :visible.sync="editVisible2" width="380px">
            <el-input class="margin10" v-model="input" size="small" placeholder="班级名称"></el-input>
            <p class="red">* 新建班级名称不能与现有班级重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible2=false" plain>取 消</el-button>
            </div>
        </el-dialog>
        <!-- 新建入学年份 -->
        <el-dialog title="新建入学年份" class="datas-bg" :visible.sync="editVisible3" width="380px">
            <el-input class="margin10" v-model="input" size="small" placeholder="年份"></el-input>
            <p class="red">* 年份只能是数字</p>
            <p class="red">* 新建年份不能与现有年份重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible3=false" plain>取 消</el-button>
            </div>
        </el-dialog>
    </div> 
</template>

<script>
import Aschoolmanage2 from './Aschoolmanage2.vue'
export default {
    name: 'course',
    components:{Aschoolmanage2},
    data() {
        return {
            editVisible:false,
            editVisible2:false,
            editVisible3:false,
            choosed:'院系管理',
            items:[1,1,1],
            tableData5: [{
              title: '院系：机电工程学院',
              datas: [{name:'班级 15134646416'},
              {name:'班级：15134646416'},
              {name:'班级：15134646416'}]
            }, {
              title: '院系：软件工程学院',
              datas: [{name:'班级：15134646416'},
              {name:'班级：15134646416'}]
            }]
        };
    },
    methods: {
      delworks(){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        }
    }
};
</script>
<style lang='scss'>
  .cont{
      tbody{
        .el-table__expanded-cell{
          padding:5px 0 5px 60px;
        }
      }
    }
</style>
<style lang='scss' scoped>
    .red{
      color:red;
      font-size:12px;
      line-height:16px;
      padding-top:5px;
    }
    .cont{
      padding-top:20px;
      .class-bg{
        overflow:hidden;
        line-height:32px;
        font-size:13px;
        color:#666;
      }
      .college{
        margin-right:-10px;
      }
    }
    .pagination{
        margin-top:20px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
</style>


